import baseURL from '@/config/index.js'
import loginModal from '../../components/Modal/loginModal.vue'
export default {
	data() {
		return {
			privateObj: {
				searchHeight: 0, // 搜索框高度，单位px
				searchTop: 0, // 搜索框距离顶部距离
			},
			loginModalShow:false,
			payShow:false,
			userName:'未登录',
			mobile:'',
			avatar:'',
			myEduMoney: 0,
			myMoney: 0,
			QRUrl: ''
		}
	},
	components:{
		loginModal
	},
	methods:{
		// 我的订单
		goToMyOrder () {
			uni.navigateTo({
				url: '../order/order'
			})
		},
		// 我的拼团
		goToMyGroup () {
			uni.navigateTo({
				url: '../GroupOrder/GroupOrder'
			})
		},
		// 邀请好友
		goToMyInvite (QRUrl) {
			uni.navigateTo({
				url: '../invite/invite?QRUrl=' + QRUrl
			})
		},
		// 设置中心
		goToSetting () {
			uni.navigateTo({
				url: './SystemSetting'
			})
		},
		
		// 我的信息
		goToMyInfo () {
			uni.navigateTo({
				url: './MyInfo'
			})
		},
		// 我的金币
		goToMyMoney () {
			uni.navigateTo({
				url: '../MyMoney/Mymoney'
			})
		},
		// 我的学币
		goToMyEduMoney () {
			uni.navigateTo({
				url: '../MyStudyMoney/MyStudyMoney'
			})
		},
		// 关闭授权弹窗
		ModalClose(data){
			this.loginModalShow = data
			this.getUserInfo()
		},
		// 二维码
		async initGetQRCode () {
			let res = await this.$fetch(this.$api.getQrCode,{},'GET','FORM')
			this.QRUrl = baseURL +　res.data.slice(1)
			console.log(this.QRUrl)
		},
		// 获取个人信息
		async getUserInfo(){
			
			let res = await this.$fetch(this.$api.userInfo,{},'GET', 'form')
			this.userName = res.data.userName
			this.mobile = res.data.phonenumber
			this.avatar = res.data.avatar
			this.myEduMoney = Number(res.data.money).toFixed(2)
			this.myMoney = Number(res.data.reward).toFixed(2)
			
			if (!this.myMoney) {
				this.myMoney = 0
			}
			if (!this.myEduMoney) {
				this.myEduMoney = 0
			}
			let userInfo = {
				userAvatar: this.avatar,
				userName: this.userName,
				userId: res.data.userId
			}
			uni.setStorageSync('userInfo', userInfo)
			
			if (res.data.userId) {
				uni.setStorageSync('userId', res.data.userId)
			}
			let classTrue = {
				id: res.data.classTypeId,
				name: res.data.classType.name
			}
			uni.setStorageSync('classTrue', JSON.stringify(classTrue))
			uni.setStorageSync('classTrueId', res.data.classTypeId)
			uni.setStorageSync('classBackFresh',true)
			this.initGetQRCode()
			
			
		},
		// 注册完成后重新获取个人信息
		getFresh(data){
			console.log(data)
			if(data){
				this.getUserInfo()
			}
		},
		
	},
	onShow() {
		if(!uni.getStorageSync('token')){
			console.log('12313')
			this.loginModalShow = true
		}else{
			this.getUserInfo()
		}
		setTimeout(() => {
			if(uni.getStorageSync('showLogin')) {
				console.log('5555')
				this.loginModalShow = uni.getStorageSync('showLogin')
			}
		}, 300)
		
	},
	
	
}
